import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { UserSearchListComponent } from './user-search-list.component';
import { LoderModule } from 'src/app/common/loder/loder.module';
import { PaginationModule } from 'src/app/common/pagination/pagination.module';
import { UserSearchService } from 'src/app/service/user-search.service';

const routes: Routes = [
  {
    path: '',
    component: UserSearchListComponent,
    // data: { title: 'company_list' }
  },
  {
    path: 'add',
    loadChildren: () => import('../user-search-add/user-search-add.module').then(m => m.UserSearchAddModule),
    // data: { title: 'add_company' }
  },
  {
    path: 'edit/:id',
    loadChildren: () => import('../user-search-add/user-search-add.module').then(m => m.UserSearchAddModule),
    // data: { title: 'edit_company' }
  },
];

@NgModule({
  declarations: [
    UserSearchListComponent
  ],
  imports: [
    CommonModule,
    LoderModule,
    PaginationModule,
    RouterModule.forChild(routes)
  ],
  providers: [
    UserSearchService
  ]
})
export class UserSearchListModule { }
